import React, { useState, useCallback, useEffect } from 'react';
import { Box, Container, makeStyles } from '@material-ui/core';
import Page from '../../../components/Page';
import useIsMountedRef from '../../../hooks/useIsMountedRef';
import BranchEditForm from './BranchEditForm';
import Header from './Header';
import { useParams } from 'react-router-dom';
import branchService from '../../../services/branchService';
import { useTranslation } from 'react-i18next';

const useStyles = makeStyles((theme) => ({
  root: {
    backgroundColor: theme.palette.background.dark,
    minHeight: '100%',
    paddingTop: theme.spacing(3),
    paddingBottom: theme.spacing(3)
  }
}));

function BranchEditView() {
  const { id } = useParams();
  const classes = useStyles();
  const isMountedRef = useIsMountedRef();
  const [branch, setBranch] = React.useState();
  const [branches, setBranches] = React.useState();
  const { t } = useTranslation();

  const getBranch = useCallback(async () => {
    const data = await branchService.get(id);
    setBranch(data.data.data);
  }, [isMountedRef]);

  const getBranches = useCallback(async () => {
    const data = await branchService.list();
    setBranches(data.data.data);
  }, [isMountedRef]);

  useEffect(() => {
    getBranch();
    getBranches();
  }, [getBranch, getBranches]);

  if (!branch) {
    return null;
  }

  if (!branches) {
    return null;
  }

  return (
    <Page className={classes.root} title={t('Edit Branch')}>
      <Container maxWidth="lg">
        <Header />
        <Box mt={3}>
          <BranchEditForm branch={branch} branches={branches} />
        </Box>
      </Container>
    </Page>
  );
}

export default BranchEditView;
